import React from 'react';
import { motion } from 'framer-motion'; // Import motion

const DashboardCard = ({ title = '', value = '', icon: Icon = null, theme = {}, onClick }) => (
  <motion.div
    whileHover={{ scale: 1.02, boxShadow: `0 8px 15px -3px ${theme.primary || '#000'}` }}
    transition={{ duration: 0.2 }}
    className={`relative p-6 rounded-xl shadow-lg flex flex-col justify-between cursor-pointer`}
    style={{ backgroundColor: theme.secondary || '#fff', color: theme.text || '#000' }}
    onClick={onClick}
  >
    <div className="flex items-start justify-between mb-4">
      <h3 className={`text-sm font-bold`} style={{ color: theme.accent || '#000' }}>{String(title)}</h3>
      <div className={`w-10 h-10 rounded-full flex items-center justify-center`} style={{ backgroundColor: theme.special || '#eee' }}>
        {Icon ? <Icon style={{ color: theme.text || '#000' }} size={24} /> : null}
      </div>
    </div>
    <div>
      <p className={`text-2xl font-bold text-white whitespace-nowrap overflow-hidden text-ellipsis`}>{String(value)}</p>
      <p className={`text-sm font-medium`} style={{ color: theme.accent || '#000' }}>+12% dari bulan lalu</p>
    </div>
  </motion.div>
);

export default DashboardCard;
