import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, BarChart, Bar } from 'recharts';
import { TrendingUp, TrendingDown, Wallet, Briefcase } from 'lucide-react';
import DashboardCard from '../components/DashboardCard.jsx';

const apiKey = "AIzaSyCftKtTlvuV6UL5ZdMNkdsw-ny8EuOqxo4"; // Gemini API Key

async function getGeminiResponse() {
  const url = "https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent";
  const headers = {
    "Content-Type": "application/json",
    "X-goog-api-key": apiKey,
  };
  const body = JSON.stringify({
    contents: [
      {
        parts: [
          { text: "Explain how AI works in a few words" }
        ]
      }
    ]
  });

  try {
    const response = await fetch(url, {
      method: "POST",
      headers,
      body,
    });
    const data = await response.json();
    console.log(data); // Lihat hasil di console
    // Lakukan sesuatu dengan data di sini
  } catch (error) {
    console.error("Error:", error);
  }
}

const DashboardContent = ({ theme = {}, financialSummary = {}, financeData = [] }) => {
  return (
    <>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <DashboardCard
          title="Total Pendapatan"
          value={String(financialSummary.revenue || '-')}
          icon={TrendingUp}
          theme={theme}
        />
        <DashboardCard
          title="Total Pengeluaran"
          value={String(financialSummary.expenses || '-')}
          icon={TrendingDown}
          theme={theme}
        />
        <DashboardCard
          title="Laba Bersih"
          value={String(financialSummary.profit || '-')}
          icon={Wallet}
          theme={theme}
        />
        <DashboardCard
          title="Saldo Kas"
          value={String(financialSummary.cash || '-')}
          icon={Briefcase}
          theme={theme}
        />
      </div>
      <div className={`p-6 rounded-xl shadow-lg`} style={{ backgroundColor: theme.secondary }}>
        <h3 className={`text-lg font-bold mb-4`} style={{ color: theme.text }}>Grafik Laba/Rugi Bulanan</h3>
        {Array.isArray(financeData) && financeData.length > 0 ? (
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={financeData}>
              <CartesianGrid strokeDasharray="3 3" stroke={`#404040`} />
              <XAxis dataKey="month" stroke={`#a3a3a3`} />
              <YAxis stroke={`#a3a3a3`} />
              <Tooltip contentStyle={{ backgroundColor: `#262626`, border: 'none', color: `#fff`, borderRadius: '8px' }} />
              <Legend />
              <Bar dataKey="Pendapatan" fill="#00b4d8" />
              <Bar dataKey="Pengeluaran" fill="#ef4444" />
            </BarChart>
          </ResponsiveContainer>
        ) : (
          <div className="text-center text-gray-400">Data grafik tidak tersedia</div>
        )}
      </div>
      <button onClick={getGeminiResponse} className="px-4 py-2 bg-blue-600 text-white rounded shadow hover:bg-blue-700 mb-4">
        Jalankan Gemini API
      </button>
    </>
  );
};

export default DashboardContent;
