import React, { useState, useEffect } from 'react';
import {
  Home,
  Package,
  Wallet,
  ShoppingCart,
  Users,
  BarChart as BarChartIcon,
  Settings,
  ArrowRight,
  Menu,
  X,
  CreditCard,
  Building2,
  Image as ImageIcon,
  CheckCircle,
  Layers,
  ShoppingBag,
  Heart,
  UserCheck,
  Globe,
  DollarSign,
  FileText,
  Briefcase,
  TrendingUp,
  TrendingDown,
  Receipt,
  Truck,
  BellRing,
  Search,
  Filter,
  MoreVertical,
  ClipboardList,
  MessageSquare,
  ChevronDown,
  ArrowDownLeft,
  ArrowUpRight,
  Info,
  Edit,
  Trash2
} from 'lucide-react';

// Import components
import Sidebar from './components/Sidebar.jsx';

// Import pages
import DashboardContent from './pages/DashboardContent.jsx';
import InventoryContent from './pages/InventoryContent.jsx';
import SalesEcommerceContent from './pages/SalesEcommerceContent.jsx';
import SalesOfflineContent from './pages/SalesOfflineContent.jsx';
import EmployeesContent from './pages/EmployeesContent.jsx';
import CrmContent from './pages/CrmContent.jsx';
import ReportsContent from './pages/ReportsContent.jsx';
import IntegrationsContent from './pages/IntegrationsContent.jsx';
import SettingsContent from './pages/SettingsContent.jsx';
import FinanceDashboardContent from './pages/FinanceDashboardContent.jsx';
import PemasukanContent from './pages/PemasukanContent.jsx';
import PengeluaranContent from './pages/PengeluaranContent.jsx';
import HutangPiutangContent from './pages/HutangPiutangContent.jsx';
import SaldoKasContent from './pages/SaldoKasContent.jsx';

// Import data
import {
  themes,
  initialInventoryItems,
  salesByPlatformData,
  financialSummary,
  initialReceivables,
  initialPayables,
  bestSellingProducts,
  initialOfflineSales,
  initialEcommerceOrders,
  financeData, // Added financeData import
  employeesList, // Added employeesList import
  customerList // Added customerList import
} from './data/initialData.js';


export default function App() {
  const [activePage, setActivePage] = useState('dashboard');
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const [storeName, setStoreName] = useState('User Name');
  const [storeLogo, setStoreLogo] = useState('/KHo0y7f.md.png'); // Path to public folder
  const [theme, setTheme] = useState(themes[0]);
  const [receivablesList, setReceivablesList] = useState(initialReceivables);
  const [payablesList, setPayablesList] = useState(initialPayables);
  const [offlineSales, setOfflineSales] = useState(initialOfflineSales);
  const [inventoryItems, setInventoryItems] = useState(initialInventoryItems);
  const [ecommerceOrders, setEcommerceOrders] = useState(initialEcommerceOrders);

  const lowStockItems = inventoryItems.filter(item => item.stock < item.reorderPoint);

  const renderContent = () => {
    switch (activePage) {
      case 'dashboard':
        return <DashboardContent theme={theme} financialSummary={financialSummary} salesByPlatformData={salesByPlatformData} bestSellingProducts={bestSellingProducts} receivables={receivablesList} payables={payablesList} setActivePage={setActivePage} lowStockItems={lowStockItems} />;
      case 'inventory':
        return <InventoryContent theme={theme} bestSellingProducts={bestSellingProducts} inventoryItems={inventoryItems} setInventoryItems={setInventoryItems} />;
      case 'sales-ecommerce':
        return <SalesEcommerceContent theme={theme} ecommerceOrders={ecommerceOrders} setEcommerceOrders={setEcommerceOrders} />;
      case 'sales-offline':
        return <SalesOfflineContent theme={theme} offlineSales={offlineSales} setOfflineSales={setOfflineSales} />;
      case 'employees':
        return <EmployeesContent theme={theme} employeesList={employeesList} />;
      case 'crm':
        return <CrmContent theme={theme} customerList={customerList} />;
      case 'reports':
        return <ReportsContent theme={theme} />;
      case 'integrations':
        return <IntegrationsContent theme={theme} />;
      case 'settings':
        return <SettingsContent
          theme={theme} setTheme={setTheme} setStoreLogo={setStoreLogo}
        />;
      case 'finance-dashboard':
        return <FinanceDashboardContent theme={theme} financialSummary={financialSummary} financeData={financeData} />;
      case 'finance-pemasukan':
        return <PemasukanContent theme={theme} />;
      case 'finance-pengeluaran':
        return <PengeluaranContent theme={theme} />;
      case 'finance-payables':
        return <HutangPiutangContent
          theme={theme}
          receivables={receivablesList}
          payables={payablesList}
          setReceivablesList={setReceivablesList}
          setPayablesList={setPayablesList}
        />;
      case 'finance-saldokas':
        return <SaldoKasContent theme={theme} financialSummary={financialSummary} />;
      default:
        // Set default view based on the active menu parent
        if (activePage.startsWith('sales')) {
            return <SalesEcommerceContent theme={theme} ecommerceOrders={ecommerceOrders} setEcommerceOrders={ecommerceOrders} />;
        }
        if (activePage.startsWith('finance')) {
            return <FinanceDashboardContent theme={theme} financialSummary={financialSummary} financeData={financeData} />;
        }
        return <DashboardContent theme={theme} financialSummary={financialSummary} salesByPlatformData={salesByPlatformData} bestSellingProducts={bestSellingProducts} receivables={initialReceivables} payables={initialPayables} />;
    }
  };

  return (
    <div className={`flex min-h-screen font-sans antialiased`} style={{ backgroundColor: theme.background, color: theme.text }}>
      {/* Sidebar */}
      <Sidebar active={activePage} setActive={setActivePage} isSidebarOpen={isSidebarOpen} setSidebarOpen={setSidebarOpen} storeName={storeName} theme={theme} storeLogo={storeLogo} />

      {/* Main Content */}
      <div className="flex-1 flex flex-col p-4 md:p-8">
        {/* Header for mobile */}
        <div className={`sticky top-0 pb-4 mb-4 md:hidden flex items-center justify-between z-30`} style={{ backgroundColor: theme.background }}>
          <div className="flex items-center">
            {/* Logo, nama aplikasi, dan nomor versi untuk mobile */}
            <div className="flex items-center">
              <img src={storeLogo} alt="Logo Toko" className="w-10 h-10 rounded-full mr-3" />
              <div className="flex flex-col">
                <div className="flex items-center">
                  <h1 className={`text-2xl font-bold`} style={{ color: theme.text }}>Etyzeen</h1>
                  <span className={`text-sm font-semibold ml-1`} style={{ color: theme.accent }}>
                    ERP
                    <span className={`bg-blue-500 text-white font-bold text-xs px-2 py-0.5 rounded-full ml-2`}>PRO</span>
                  </span>
                </div>
                <span className={`text-xs font-light`} style={{ color: theme.accent }}>v1.0.31</span>
              </div>
            </div>
          </div>
          <button className={`p-2 rounded-lg`} style={{ color: theme.accent, backgroundColor: theme.secondary }} onClick={() => setSidebarOpen(true)}>
            <Menu size={24} />
          </button>
        </div>

        <header className="flex items-center justify-between mb-8">
          <h1 className={`text-2xl md:text-3xl font-bold capitalize`} style={{ color: theme.text }}>
            {activePage === 'dashboard' ? 'Dashboard' : activePage === 'inventory' ? 'Manajemen Stok' : activePage === 'finance-dashboard' ? 'Dashboard Keuangan' : activePage === 'finance-pemasukan' ? 'Pemasukan' : activePage === 'finance-pengeluaran' ? 'Pengeluaran' : activePage === 'finance-payables' ? 'Hutang Piutang' : activePage === 'finance-saldokas' ? 'Saldo Kas' : activePage === 'sales-ecommerce' ? 'E-commerce' : activePage === 'sales-offline' ? 'Toko Fisik' : activePage}
          </h1>
          <div className="hidden md:flex items-center space-x-4">
            <span className={`text-sm font-medium`} style={{ color: theme.accent }}>Selamat datang, {storeName}!</span>
          </div>
        </header>

        <main className="flex-1">
          {renderContent()}
        </main>
      </div>
    </div>
  );
}
